@tailwind base;
@tailwind components;
@tailwind utilities;

/* Typography and layout base */
body {
  @apply font-sans bg-gradient-to-tr from-white via-blue-50 to-white text-gray-800 leading-relaxed;
}

/* Enhance scroll behavior */
html {
  scroll-behavior: smooth;
}

/* Headings */
h1, h2, h3, h4, h5, h6 {
  @apply tracking-tight font-extrabold text-blue-800;
}

/* Buttons */
.button-primary {
  @apply bg-gradient-to-r from-blue-600 to-indigo-600 text-white font-semibold py-2 px-6 rounded-lg shadow-md transition transform hover:scale-105 hover:from-indigo-600 hover:to-blue-700;
}

/* Cards */
.card {
  @apply rounded-xl shadow-lg p-6 bg-white hover:shadow-2xl transition duration-300 ease-in-out;
}

/* Image hover effect */
.card-image-hover {
  @apply rounded-lg shadow-lg transition-transform duration-300 transform hover:scale-105 hover:brightness-110;
}

/* Input and textarea */
input, textarea {
  @apply border border-gray-300 rounded-md px-4 py-3 w-full focus:outline-none focus:ring-2 focus:ring-blue-600 transition duration-200;
}

/* Section titles */
.section-title {
  @apply text-4xl md:text-5xl font-bold text-center text-blue-800 mb-10;
}

/* Smooth fade-in animation */
.fade-in {
  animation: fadeIn 1s ease-in-out;
}
@keyframes fadeIn {
  from { opacity: 0; transform: translateY(20px); }
  to { opacity: 1; transform: translateY(0); }
}

/* Link styles */
a {
  @apply text-blue-600 hover:text-indigo-600 transition no-underline;
}


/* Mobile nav enhancements */
.mobile-nav-link {
  @apply block px-4 py-2 text-sm font-medium hover:text-blue-700;
}

/* Footer styling (if you extend it) */
footer {
  @apply bg-blue-100 text-gray-700 py-6 text-center mt-20;
}

/* Divider styles */
.section-divider {
  @apply my-20 border-t-2 border-dotted border-blue-300 w-1/2 mx-auto;
}

/* Gradient text utility */
.gradient-text {
  background: linear-gradient(to right, #2563eb, #4f46e5);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}
/* Contact Info Container */
.contact-info {
  @apply bg-gradient-to-tr from-blue-50 to-indigo-50 rounded-2xl shadow-xl p-10 flex flex-col justify-center items-center text-center text-gray-700 space-y-8;
}

/* Heading */
.contact-info h3 {
  @apply text-3xl font-extrabold text-blue-800 tracking-wide;
}

/* Description paragraph */
.contact-info p.description {
  @apply text-lg max-w-xs mx-auto text-gray-600 font-medium;
}

/* Email link */
.contact-info a.email-link {
  @apply text-blue-700 font-semibold text-xl underline hover:text-indigo-700 transition;
}

/* Social links container */
.contact-info .social-links {
  @apply mt-6 flex flex-col gap-3 text-blue-700 text-base font-semibold;
}


/* Individual social link styling */
.contact-info .social-links a {
  @apply underline hover:text-indigo-700 transition;
}
/* ===== Subtle professional background ===== */
body {
  background: 
    linear-gradient(135deg, #e0f2fe 0%, #f0f9ff 100%), /* soft blue gradient */
    url('https://www.transparenttextures.com/patterns/cubes.png'); /* subtle tech pattern */
  background-repeat: repeat;
  background-size: auto;
  background-blend-mode: overlay;
  position: relative;
  min-height: 100vh;
}

/* White translucent overlay with blur behind content */
#root::before {
  content: "";
  position: fixed;
  top: 0; left: 0; right: 0; bottom: 0;
  pointer-events: none;
  background: rgba(255, 255, 255, 0.6);
  backdrop-filter: blur(8px);
  z-index: -1;
}

/* Container for page content: white with opacity and padding */
.content-container {
  @apply bg-white bg-opacity-90 rounded-lg shadow-lg p-6 max-w-5xl mx-auto my-10;
}
